.previous-searches{
  display: block;
  h2{
      color: var(--text-color);
      margin-bottom: .5em;
  }
  
  .previous-searches-container{
    .search-item{background-color:var(--primary-color);
    display: inline-block;
    padding: .5em 1em; 
    margin: 0 .5em .5em 0;
    color:#fff;
    border-radius: 30px;
    box-shadow: 0 3px 6px var(--shadow-color);
    opacity:0;
    transform: translateX(100%);
    animation: getAnimationDuration(.5s) slideInRight forwards;
    }
  }
  .search-box{
    display: flex;
    align-items:center;
    justify-content:flex-end;
    height: 40px;
    input, .btn{
      height: 35px;
    }
    input{
      min-width: 250px;
      padding: 0 0.5em;
      border-radius: 5px 0 0 5px;
      outline:none;
      border: 1px solid #4d4d4d;
    }
    .btn{
      padding-block: 0;
      padding-inline: .75em;
      border-radius:0 5px 5px 0;
    }
  }
 
}

